.intro{
    display: flex;
    height: 77vh;
    margin-top: 6rem;

}

.i-left{
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;
}


.i-name{
    display: flex;
    flex-direction: column;
}

.i-name>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 3rem;


}

.i-name>:nth-child(2){
    color: var(--orange);
    font-weight: bold;
    font-size: 3rem;
}

.i-name>:nth-last-child(3){
    font-weight: 100;
    font-size: 14px;
    color: var(--grey);
    margin-top: 10px;
}

.i-button{
    width: 6rem;
    height: 2rem;
}

.i-icons{
    margin-top: 3rem;
    gap: 0rem;
    display: flex;
}

.i-icons>*{
    transform: scale(0.5);
}

.i-icons>*:hover{
    cursor: pointer;
}

.i-right{
   flex: 1;
   position: relative;  
}
.i-right>*{
position: absolute;
z-index: 1;
}

.i-right>:nth-child(1){
    transform: scale(0.69);
    left: -15%;
    top: -9%;
}

.i-right>:nth-child(2){
    transform: scale(0.67);
    left: -3%;
    top: -4.6%;
}

.i-right>:nth-child(3){
    transform: scale(1.4);
    left: 28%;
}

.flotingdiv{
    justify-content: space-between;
    display: flex;
    background: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center;
    padding: 0px 28px 0px 0px;
}